import React, { useCallback, useEffect, useRef, useState } from 'react'
import { useCustomTranslation } from '../../../translation'
import { CustomButton, CustomError, CustomSpinner } from '../../globalComponents'
import { CustCheckbox, CustomStrong, Input, Label } from '../../commanComponent'
import { getInputPrefix } from '../../../utils/common'
import { useDispatch, useSelector } from 'react-redux';
import { payrollActions } from '../../../core/resource/actions'
import ApproveRejectModal from '../../modal/ApproveRejectModal'
import { validateSchema } from '../../../utils/ValidationHelper'
import { salaryTempFormDataSchema } from '../../../utils/ValidationSchema'
import debounce from 'lodash.debounce';
import { Link, useParams ,useNavigate,useLocation} from 'react-router-dom'
import BackIcon from '../../commanComponent/BackIcon'
import { FullScreenPreviewModal } from '../../employee/onboarding-process'
const CreateUpdateSalaryTemplate = (props) => {
        const navigate =useNavigate()
    const { selectedSalaryTemplate, employeeID ,salaryUpdate} = props
    const dispatch = useDispatch();
    const location = useLocation();
    const { t } = useCustomTranslation();
    const [errors, setErrors] = useState("");
    const [selectedSalaryTempData, setSelectedSalaryTempData] = useState('')
    const [employeeSalaryDetail, setEmployeeSalaryDetail] = useState('');
    const [isOpenDeleteModal, setIsOpenDeleteModal] = useState(false);
    const [selectedEarningArr, setSelectedEarningArr] = useState([]);
    const [selectedDeductionArr, setSelectedDeductionArr] = useState([]);
    const [getAllEarning, setAllEarning] = useState([]);
    const [getAllDeduction, setAllDeduction] = useState([]);
    const [openPdf, setOpenPdf] = useState(false)
    const [offerLetterData, setOfferLetter] = useState('')
    const [offerLetterDetails, setOfferLetterDetails] = useState(null)
    const [selectedDeleteData, setSelectedDeleteData] = useState({
        type: '',
        data: '',
        index: ''
    })
    const [salaryTempFormData, setSalaryTempFormData] = useState({
        templateName: '',
        description: '',
        ctc: '',
    });

    const {
        getAllEarningPending,
        getAllEarningRes,
        getAllEarningError,

        getAllDeductionPending,
        getAllDeductionRes,
        getAllDeductionError,

        generateSalaryTemplatePending,
        generateSalaryTemplateRes,
        generateSalaryTemplateError,

        calculateCtcPending,
        calculateCtcRes,
        calculateCtcError,

        saveSalaryTemplatePending,
        saveSalaryTemplateRes,
        saveSalaryTemplateError,

        getAllSalaryTemplatePending,
        getAllSalaryTemplateRes,
        getAllSalaryTemplateError,

        updateSalaryTemplatePending,
        updateSalaryTemplateRes,
        updateSalaryTemplateError,

        getSalaryTemplateByIdPending,
        getSalaryTemplateByIdRes,
        getSalaryTemplateByIdError,

        addEmployeeSalaryPending,
        addEmployeeSalaryRes,
        addEmployeeSalaryError,

        sendOfferLetterPending,
        sendOfferLetterRes,
        sendOfferLetterError,

    } = useSelector(state => state.payroll)
    const { id } = useParams()
    useEffect(() => {
    if(salaryUpdate){
        setEmployeeSalaryDetail(selectedSalaryTemplate)
    }else{
        if (id || selectedSalaryTemplate) {
            dispatch(payrollActions.getSalaryTemplateByIdRequest({ _id: id || selectedSalaryTemplate?._id }))
            dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_GENERATE_SALARY_TEMPLATE_STORE"))
        }
    } 
    }, [id || selectedSalaryTemplate])

    useEffect(() => {
        if (!getSalaryTemplateByIdPending) {
            if (getSalaryTemplateByIdError) {
                return;
            }
            if (getSalaryTemplateByIdRes) {
                if (getSalaryTemplateByIdRes?.code === 200) {
                    const salaryTemplateByIdResData = getSalaryTemplateByIdRes?.data || [];
                    if (salaryTemplateByIdResData && salaryTemplateByIdResData?.length > 0) {
                        setSelectedSalaryTempData(salaryTemplateByIdResData)
                        setSalaryTempFormData({
                            templateName: salaryTemplateByIdResData?.[0]?.template_name,
                            description: salaryTemplateByIdResData?.[0]?.description,
                            ctc: salaryTemplateByIdResData?.[0]?.ctc?.monthly,
                        });
                        setEmployeeSalaryDetail(salaryTemplateByIdResData?.[0])
                    }
                }
            }
        }
    }, [
        getSalaryTemplateByIdRes,
        getSalaryTemplateByIdError,
        getSalaryTemplateByIdPending
    ])

    useEffect(() => {
        if (!calculateCtcPending) {
            if (calculateCtcError) {
                dispatch(payrollActions.handleClearPayrollStore("HANDLE_CALCULATE_CTC_STORE"))
                return;
            }
            if (calculateCtcRes) {
                if (calculateCtcRes?.code === 200) {
                    setEmployeeSalaryDetail(calculateCtcRes?.data)
                    dispatch(payrollActions.handleClearPayrollStore("HANDLE_CALCULATE_CTC_STORE"))
                }
            }
        }
    }, [
        calculateCtcRes,
        calculateCtcError,
        calculateCtcPending
    ])

    useEffect(() => {
        if (!calculateCtcPending) {
            if (calculateCtcError) {
                dispatch(payrollActions.handleClearPayrollStore("HANDLE_CALCULATE_CTC_STORE"))
                return;
            }
            if (calculateCtcRes) {
                if (calculateCtcRes?.code === 200) {
                    setEmployeeSalaryDetail(calculateCtcRes?.data)
                    dispatch(payrollActions.handleClearPayrollStore("HANDLE_CALCULATE_CTC_STORE"))
                }
            }
        }
    }, [
        calculateCtcRes,
        calculateCtcError,
        calculateCtcPending
    ])


    useEffect(() => {
        if (!generateSalaryTemplatePending) {
            if (generateSalaryTemplateError) {
                dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_GENERATE_SALARY_TEMPLATE_STORE"))
                return;
            }
            if (generateSalaryTemplateRes) {
                if (generateSalaryTemplateRes?.code === 200) {
                    setEmployeeSalaryDetail(generateSalaryTemplateRes?.data)
                    dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_GENERATE_SALARY_TEMPLATE_STORE"))
                }
            }
        }
    }, [
        generateSalaryTemplateRes,
        generateSalaryTemplateError,
        generateSalaryTemplatePending
    ])

    useEffect(() => {
        if (!getAllEarningPending) {
            if (getAllEarningError) {
                return;
            }
            if (getAllEarningRes) {
                if (getAllEarningRes?.code === 200) {
                    setAllEarning(getAllEarningRes?.data)
                }
            }
        }
    }, [
        getAllEarningRes,
        getAllEarningError,
        getAllEarningPending
    ])

    useEffect(() => {
        if (!getAllDeductionPending) {
            if (getAllDeductionError) {
                return;
            }
            if (getAllDeductionRes) {
                if (getAllDeductionRes?.code === 200) {
                    setAllDeduction(getAllDeductionRes?.data)
                }
            }
        }
    }, [
        getAllDeductionRes,
        getAllDeductionError,
        getAllDeductionPending
    ])
    /* save Salary Template */
    useEffect(() => {
        if (!saveSalaryTemplatePending) {
            if (saveSalaryTemplateError) {
                dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_SAVE_SALARY_TEMPLATE_STORE"))
                return;
            }
            if (saveSalaryTemplateRes) {
                if (saveSalaryTemplateRes?.code === 200) {
                    setEmployeeSalaryDetail('')
                    setSalaryTempFormData('')
                    setSelectedDeductionArr('')
                    setSelectedEarningArr('')
                    dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_SAVE_SALARY_TEMPLATE_STORE"))
                }
            }
        }
    }, [
        saveSalaryTemplateRes,
        saveSalaryTemplateError,
        saveSalaryTemplatePending
    ])
    /* Update Salary Template */
    useEffect(() => {
        if (!updateSalaryTemplatePending) {
            if (updateSalaryTemplateError) {
                dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_UPDATE_SALARY_TEMPLATE_STORE"))
                return;
            }
            if (updateSalaryTemplateRes) {
                if (updateSalaryTemplateRes?.code === 200) {
                    dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_UPDATE_SALARY_TEMPLATE_STORE"))
                }
            }
        }
    }, [
        updateSalaryTemplateRes,
        updateSalaryTemplateError,
        updateSalaryTemplatePending
    ])

    /* Add/Assigned Employee Salary  */
    useEffect(() => {
        if (!addEmployeeSalaryPending) {
            if (addEmployeeSalaryError) {
                dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_ADD_EMPLOYEE_SALARY_STORE"))
                return;
            }
            if (addEmployeeSalaryRes) {
                if (addEmployeeSalaryRes?.code === 200) {

                    if(location.pathname === '/employee/add-employee-salary'){
                        setOfferLetterDetails(addEmployeeSalaryRes.data)
                        sendOfferLetterToCandidate(addEmployeeSalaryRes.data,false)
                    }else{
                      navigate("/payroll-employees")
                    }
                    dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_ADD_EMPLOYEE_SALARY_STORE"))
                }
            }
        }
    }, [
        addEmployeeSalaryRes,
        addEmployeeSalaryError,
        addEmployeeSalaryPending
    ])
         /* Add/Assigned Employee Salary  */
            useEffect(() => {
                if (!sendOfferLetterPending) {
                    if (sendOfferLetterError) {
                        dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_SEND_OFFER_LETTER_STORE"))
                        return;
                    }
                    if (sendOfferLetterRes) {
                        if (sendOfferLetterRes?.code === 200) {
                            if(!sendOfferLetterRes.data){
                               navigate("/employee")
                            }else{
                            setOpenPdf(true)
                            setOfferLetter({
                                filename: 'Offer Letter',
                                fileUrl: sendOfferLetterRes?.data?.filePath || '',
                                fileType: 'application/pdf',
                            }) }
                            dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_SEND_OFFER_LETTER_STORE"))
                        }
                    }
                }
            }, [
                sendOfferLetterPending,
                sendOfferLetterRes,
                sendOfferLetterError,
            ])

    const sendOfferLetterToCandidate =(data, type)=>{
        let payload
        if(type){
            payload={
                salary_id:offerLetterDetails?.id,
                employee_id:offerLetterDetails?.employee_id,
                is_preview:true
            }
        }else{
            payload = {
                salary_id:data?.id,
                employee_id:data?.employee_id,
                is_preview:false
            } 
        }
            dispatch(payrollActions.sendOfferLetterRequest(payload))
        }

    const sendOfferLetterMail = ()=>{
        sendOfferLetterToCandidate('',true)
    }
    const assignedSalaryTemp = () => {
     
        const payload = {
            employee_id: employeeID,
            earnings:employeeSalaryDetail?.earnings,
            deductions:employeeSalaryDetail?.deductions,
            bonus:employeeSalaryDetail?.bonus,
            ctc:employeeSalaryDetail?.ctc,
            gross:employeeSalaryDetail?.gross,
            total_employee:employeeSalaryDetail?.total_employee,
            total_employer:employeeSalaryDetail?.total_employer,
            total_ctc:employeeSalaryDetail?.total_ctc,
            take_home:employeeSalaryDetail?.take_home
            
        }
        dispatch(payrollActions.addEmployeeSalaryRequest(payload))

    }

    const submitSalaryTemp = async () => {
        const validationResponse = await validateSchema(salaryTempFormDataSchema, salaryTempFormData);
        if (validationResponse.isValid) {
            let payload = {
                ...employeeSalaryDetail,
                template_name: salaryTempFormData?.templateName,
                description: salaryTempFormData?.description
            }
            if (id) {
                payload["_id"] = id
                dispatch(payrollActions.updateSalaryTemplateRequest(payload))
            } else {
                dispatch(payrollActions.saveSalaryTemplateRequest(payload))
            }
        } else {
            setErrors(validationResponse.errors);
        }


    }

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e?.target : { value: e };

        setSalaryTempFormData(prevState => {
            let updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            return updatedState;
        });


        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };


    /* handel CTC changes */
    const employeeSalaryDetailRef = useRef(employeeSalaryDetail);

    const handleCTCFromChange = (e, index, selectItem, addType) => {
        const { value } = e.target;
        setEmployeeSalaryDetail((prevState) => ({
            ...prevState,
            [addType]: prevState[addType]?.map((item, ind) => {
                if (ind === index) {
                    return {
                        ...item,
                        value: Number(value),
                    };
                }
                return item;
            }),
        }));
        debouncedReCalculate();
    };


    const handleOnCtcReCalculate = useCallback(() => {
        const { earnings, deductions } = employeeSalaryDetailRef.current;
        let data = {
            ctc: parseInt(salaryTempFormData?.ctc, 10),
            earnings: earnings,
            deductions: deductions,
            is_include_bonus: 1,
            meal_allowance: "",
        };
        dispatch(payrollActions.calculateCtcRequest(data));
    }, [employeeSalaryDetail, salaryTempFormData, dispatch]);

    // const debouncedReCalculate = useCallback(
    //     debounce(() => {
    //         handleOnCtcReCalculate();
    //     }, 3000),
    //     [handleOnCtcReCalculate]
    // );

    const debouncedReCalculate = useCallback(
        (args) => {
            const delay = args === "delete" ? 1000 : 3000;
            const debouncedFunction = debounce(() => {
                handleOnCtcReCalculate();
            }, delay);
            debouncedFunction();

            return () => debouncedFunction.cancel();
        },
        [handleOnCtcReCalculate]
    );




    const toggleDeleteModal = (type, deleteType, item, index) => {
        if (type) {
            setEmployeeSalaryDetail((prev) => ({
                ...prev,
                [selectedDeleteData?.type]: prev[selectedDeleteData?.type].filter((_, ind) => ind !== selectedDeleteData?.index)

            }))
            setSelectedDeleteData({
                type: '',
                data: '',
                index: ''
            })
            debouncedReCalculate("delete")

        } else {
            setSelectedDeleteData({ ["type"]: deleteType, ["data"]: item, ["index"]: index })
        }
        setIsOpenDeleteModal(!isOpenDeleteModal)


    }
    const generateSalaryTemplate = async () => {

        let data = {
            ctc: parseInt(salaryTempFormData?.ctc),
            is_include_bonus: 1,
            meal_allowance: 1
        }
        if (selectedSalaryTemplate) data["template_id"] = selectedSalaryTemplate?._id
        dispatch(payrollActions.generateSalaryTemplateRequest(data));
    }



    useEffect(() => {
        dispatch(payrollActions.getAllDeductionRequest());
        dispatch(payrollActions.getAllEarningRequest())

    }, [])

    useEffect(() => {
        employeeSalaryDetailRef.current = employeeSalaryDetail;

        if (employeeSalaryDetail?.earnings?.length) {
            const eArr = employeeSalaryDetail.earnings.map((item) => item.name);
            setSelectedEarningArr(eArr);
        }

        if (employeeSalaryDetail?.deductions?.length) {
            const dArr = employeeSalaryDetail.deductions.map((item) => item.name);
            setSelectedDeductionArr(dArr);
        }
    }, [employeeSalaryDetail]);

    const isComponentDisabled = (item, type) => {
        if (type === 'earning') {
            return selectedEarningArr.includes(item?.name);
        }
        if (type === 'deduction') {
            return selectedDeductionArr.includes(item?.name);
        }
        return false;
    };

    const handleOnComponentAdd = (comp, name) => {
        const isEarning = comp === 'earning';
        const sourceArray = isEarning ? getAllEarning : getAllDeduction;
        const selectedComponent = sourceArray.find((item) => item.name === name);

        if (!selectedComponent) {
            console.warn(`Component with name "${name}" not found in ${comp} list.`);
            return;
        }

        setEmployeeSalaryDetail((prevState) => ({
            ...prevState,
            [isEarning ? 'earnings' : 'deductions']: [
                ...(prevState[isEarning ? 'earnings' : 'deductions'] || []),
                selectedComponent,
            ],
        }));

        debouncedReCalculate();
    };

    useEffect(() => {
        return () => {
            setSelectedDeleteData({
                type: '',
                data: '',
                index: ''
            })
            setSalaryTempFormData({
                templateName: '',
                description: '',
                ctc: '',

            });
            setEmployeeSalaryDetail('')
            dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_GET_SALARY_TEMPLATE_BY_ID_STORE"))
            dispatch(payrollActions.handleClearPayrollStore("HANDLE_CLEAR_GENERATE_SALARY_TEMPLATE_STORE"))
        };
    }, [])
    const renderSalaryComponentsList = (listType) => {
        const dataList = listType === 'deduction' ? getAllDeduction : getAllEarning;

        return (
            dataList &&
            dataList.map((item, index) => {
                const isDisabled = isComponentDisabled(item, listType);
                return (
                    <div
                        key={index}
                        className="accordion-body accordion-salary-component"
                    >
                        <button
                            className="btn add_new_template_sidebar_button w-100 add-comp-btn"
                            style={{ display: 'flex', justifyContent: 'space-between' }}
                            onClick={() => handleOnComponentAdd(listType, item.name)}
                            disabled={isDisabled}
                        >
                            <span>{item.payslip_name}</span>
                            <span style={{ cursor: 'pointer' }}>
                                <i className="fa fa-plus add_new_template_sidebar_button_icon"></i>
                            </span>
                        </button>
                    </div>
                );
            })
        );
    };


    const renderedCTCGenerate = () => {
        return (
            <table className=" employee-salary-table w-100" >
                <thead>
                    <tr style={{ borderBottom: "1px solid #dddddd" }}>
                        <th scope="col">Salary Components</th>
                        <th scope="col">Calculation Type</th>
                        <th scope="col">Monthly Amount</th>
                        <th scope="col">Annnual Amount</th>
                        <th scope="col"></th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <th colSpan="5">
                            <h4>Earnings</h4>
                        </th>
                    </tr>

                    {employeeSalaryDetail && employeeSalaryDetail?.earnings &&
                        employeeSalaryDetail?.earnings?.map((item, index) => (
                            <tr key={item.name}>
                                <td>{item.payslip_name}</td>
                                <td>

                                    <div className="input-group mb-3">
                                        <span className="input-group-text" id="basic-addon1">
                                            {getInputPrefix(item.value_in)}</span>
                                        <Input
                                            type="number"
                                            value={item.value}
                                            onChange={(e) => handleCTCFromChange(e, index, item, "earnings")}
                                            min={0}
                                            max={100}
                                        />

                                    </div>
                                </td>
                                <td>
                                    <div className="input-group mb-3">
                                        <Input
                                            type="number"
                                            placeholder="Enter Monthly CTC"
                                            value={item.monthly}
                                            readOnly={true}
                                            className='table-text'

                                        />
                                    </div>

                                </td>
                                <td>
                                    <div className="input-group mb-3">

                                        <CustomStrong
                                            text={item.yearly}
                                            className='table-text'
                                        ></CustomStrong>
                                    </div>


                                </td>
                                <td>
                                    {item.name !== 'basic' && (
                                        <div className='ctc-delete'
                                            onClick={() => toggleDeleteModal("", "earnings", item, index)}
                                        >
                                            <i
                                                className={`fa fa-trash text-danger cursor`}
                                                aria-hidden="true"
                                                style={{ fontSize: "16px" }}

                                            ></i>
                                        </div>
                                    )}
                                </td>
                            </tr>
                        ))}
                    <tr className='total-rows'>
                        <th colSpan={"2"}>
                            <h6 style={{ color: 'green' }}>
                                <b>GROSS</b>
                            </h6>
                        </th>

                        <th>
                            <h6 style={{ color: 'green' }} className='table-text'>
                                <b>
                                    <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                    {employeeSalaryDetail?.gross?.monthly}
                                </b>
                            </h6>
                        </th>
                        <th>
                            <h6 style={{ color: 'green' }} className='table-text'>
                                <b>
                                    <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                    {employeeSalaryDetail?.gross?.yearly}
                                </b>
                            </h6>
                        </th>
                        <th></th>
                    </tr>
                    <tr>
                        <th colSpan="5">
                            <h4 >Deductions</h4>
                        </th>
                    </tr>
                    <tr>
                        <td colSpan="5">
                            <b style={{ fontSize: "17px" }}>Employee`s Contribution</b>
                        </td>
                    </tr>
                    {employeeSalaryDetail && employeeSalaryDetail?.deductions &&
                        employeeSalaryDetail?.deductions.map((item, index) => (
                            <tr key={index}>
                                {item.name.match(/employee/i) && (
                                    <>
                                        <td>{item.payslip_name}</td>
                                        <td>
                                            <div className="input-group mb-3">
                                                <span className="input-group-text" id="basic-addon1">
                                                    {getInputPrefix(item.value_in)}</span>
                                                <Input
                                                    type="number"
                                                    value={item.value}
                                                    onChange={(e) => handleCTCFromChange(e, index, item, "deductions")}
                                                />
                                            </div>

                                        </td>
                                        <td>
                                            <div className="input-group mb-3">

                                                <Input
                                                    type="number"
                                                    placeholder="Enter Monthly CTC"
                                                    value={item.monthly}
                                                    readOnly={true}
                                                    className='table-text'
                                                />
                                            </div>
                                        </td>
                                        <td>
                                            <div className="input-group mb-3">

                                                <CustomStrong
                                                    text={item.monthly}
                                                    className='table-text'
                                                ></CustomStrong>
                                            </div>

                                        </td>
                                        <td>
                                            {item.name !== 'basic' && (
                                                <div className='ctc-delete'
                                                    onClick={() => toggleDeleteModal("", "deductions", item, index)}
                                                >
                                                    <i
                                                        className={`fa fa-trash text-danger cursor`}
                                                        aria-hidden="true"
                                                        style={{ fontSize: "16px" }}

                                                    ></i>
                                                </div>
                                            )}
                                        </td>
                                    </>
                                )}
                            </tr>
                        ))}
                    <tr className='total-rows'>
                        <th colSpan="2">
                            <h6 style={{ color: 'black' }}>
                                <b style={{ fontSize: "17px" }}>Total Employee Contribution</b>
                            </h6>
                        </th>
                        <th>
                            <h6 style={{ color: 'black' }} className='table-text'>
                                <b>
                                    <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                    {employeeSalaryDetail?.total_employee?.monthly}
                                </b>
                            </h6>
                        </th>
                        <th>
                            <h6 style={{ color: 'black' }} className='table-text'>
                                <b>
                                    <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                    {employeeSalaryDetail?.total_employee?.yearly}
                                </b>
                            </h6>
                        </th>
                        <th></th>
                    </tr>
                    <tr >
                        <td colSpan="5" >
                            <b style={{ fontSize: "17px" }}>Employer`s Contribution</b>
                        </td>
                    </tr>
                    {employeeSalaryDetail && employeeSalaryDetail?.deductions &&
                        employeeSalaryDetail.deductions.map((item, index) => (
                            <tr key={index}>
                                {(item.name.match(/employer/i) ||
                                    item.name.match(/gmc/i)) && (
                                        <>
                                            <td>{item.payslip_name}</td>
                                            <td>
                                                <div className="input-group mb-3">
                                                    <span className="input-group-text" id="basic-addon1">
                                                        {getInputPrefix(item.value_in)}</span>
                                                    <input type="text" className="form-control" placeholder="Username" aria-label="Username" aria-describedby="basic-addon1" value={item.value} />
                                                </div>
                                            </td>
                                            <td>
                                                <div className="input-group mb-3">

                                                    <Input
                                                        type="number"
                                                        placeholder="Enter Monthly CTC"
                                                        value={item.monthly}
                                                        readOnly={true}
                                                        className='table-text'
                                                    />

                                                </div>
                                            </td>
                                            <td>
                                                <div className="input-group mb-3">

                                                    <CustomStrong
                                                        text={item.yearly}
                                                        className='table-text'
                                                    ></CustomStrong>

                                                </div>


                                            </td>
                                            <td>

                                                <div className='ctc-delete'>
                                                    <i
                                                        className={`fa fa-trash text-danger cursor`}
                                                        aria-hidden="true"
                                                        style={{ fontSize: "16px" }}

                                                    ></i>
                                                </div>

                                            </td>
                                        </>
                                    )}
                            </tr>
                        ))}
                    <tr className='total-rows'>
                        <th colSpan="2" >
                            <h6 style={{ color: 'black' }}>
                                <b>Total Employer Contribution</b>
                            </h6>
                        </th>

                        <th>
                            <h6 style={{ color: 'black' }} className='table-text'>
                                <b>
                                    <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                    {employeeSalaryDetail?.total_employer?.monthly}
                                </b>
                            </h6>
                        </th>
                        <th>
                            <h6 style={{ color: 'black' }} className='table-text'>
                                <b>
                                    <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                    {employeeSalaryDetail?.total_employer?.yearly}
                                </b>
                            </h6>
                        </th>
                        <th></th>
                    </tr>
                    <tr >
                        <th colSpan="2">
                            <h6 >
                                CTC A
                            </h6>
                        </th>

                        <th>
                            <h6 className='table-text'>

                                <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                {employeeSalaryDetail?.ctc?.monthly}

                            </h6>
                        </th>
                        <th>
                            <h6 className='table-text'>

                                <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                {employeeSalaryDetail?.ctc?.yearly}

                            </h6>
                        </th>
                        <th></th>
                    </tr>
                    <tr>
                        <th colSpan="2">
                            <h6 >
                                BONUS
                            </h6>
                        </th>

                        <th>
                            <h6 className='table-text'>

                                <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                {employeeSalaryDetail?.bonus?.monthly}

                            </h6>
                        </th>
                        <th>
                            <h6 className='table-text'>

                                <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                {employeeSalaryDetail?.bonus?.yearly}

                            </h6>
                        </th>
                        <th></th>
                    </tr>
                    <tr className='total-rows'>
                        <th colSpan="2">
                            <h5>
                                TOTAL CTC
                            </h5>
                        </th>

                        <th>
                            <h5 className='table-text'>
                                <b>
                                    <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                    {employeeSalaryDetail?.total_ctc?.monthly}
                                </b>
                            </h5>
                        </th>
                        <th>
                            <h5 className='table-text'>
                                <b>
                                    <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                    {employeeSalaryDetail?.total_ctc?.yearly}
                                </b>
                            </h5>
                        </th>
                        <th></th>
                    </tr>
                    <tr className='total-rows'>
                        <th colSpan="2">
                            <h5>
                                TAKE HOME
                            </h5>
                        </th>

                        <th>
                            <h5 style={{ color: 'black' }} className='table-text'>
                                <b>
                                    <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                    {employeeSalaryDetail?.take_home?.monthly}
                                </b>
                            </h5>
                        </th>
                        <th>
                            <h5 style={{ color: 'black' }} className='table-text'>
                                <b>
                                    <i className="fa fa-inr" aria-hidden="true"></i>{' '}
                                    {employeeSalaryDetail?.take_home?.yearly}
                                </b>
                            </h5>
                        </th>
                        <th></th>
                    </tr>
                </tbody>
            </table>
        )
    }


    return (
        <div className=" myRequest mb-3 employeeHistory salary-template" >
            <div className='cardBox'>
                <div className="row">
                    <div className="col-3 salary-right-template" >
                        <div >
                            <div className="salary-temp-title">
                                <CustomStrong text={"Build Your Template"} style={{ fontSize: "18px" }} />
                                <p>Include active salary components</p>
                            </div>
                            <div className="ml-4 ">
                                <div className="accordion" id="accordionExample">
                                    <div className="accordion-item abd">
                                        <h5 className="accordion-header" id="headingOne">
                                            <button
                                                className="text-left accordion-button collapsed"
                                                type="button"
                                                data-bs-toggle="collapse"
                                                data-bs-target="#collapseOne"
                                                aria-expanded="true"
                                                aria-controls="collapseOne"
                                            >
                                                <b>Earning</b>
                                            </button>
                                        </h5>

                                        <div
                                            id="collapseOne"
                                            className="accordion-collapse collapse collapsed "
                                            aria-labelledby="headingOne"
                                            data-bs-parent="#accordionExample"
                                        >
                                            {renderSalaryComponentsList('earning')}
                                        </div>
                                    </div>
                                    <div className="accordion-item">
                                        <h5 className="accordion-header" id="headingFour">
                                            <button
                                                className="text-left accordion-button collapsed"
                                                type="button"
                                                data-bs-toggle="collapse"
                                                data-bs-target="#collapseFour"
                                                aria-expanded="true"
                                                aria-controls="collapseFour"
                                            >
                                                <b>Deductions</b>
                                            </button>
                                        </h5>
                                        <div
                                            id="collapseFour"
                                            className="accordion-collapse collapse"
                                            aria-labelledby="headingFour"
                                            data-bs-parent="#accordionExample"
                                        >
                                            {renderSalaryComponentsList('deduction')}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-9 salary-left-template " style={{ padding: "0px" }}>
                        <div className='d-flex salary-title mb-2'>
                            {!selectedSalaryTemplate && <Link to={"/salary-template"}>
                                <BackIcon />
                            </Link>}
                            <div className="header ">
                                {!selectedSalaryTemplate ? <h5
                                    className="title  fs-5 fw-bold text-primary"
                                >{selectedSalaryTempData ? "Update" : "Create "} Salary Template
                                </h5>
                                    : <h5
                                        className="title  fs-5 fw-bold text-primary"
                                    > Salary Template
                                    </h5>
                                }
                            </div>
                        </div>
                        <div className='salary-temp-body'>
                            {!selectedSalaryTemplate && <div className="row ctc-head" style={{ padding: "22px 0px" }}>
                                <div className="col-md-5">
                                    <div className="form-group mb-2">
                                        <Label text={t("labels.templateName")} required={true} />

                                        <Input
                                            type="text"
                                            value={salaryTempFormData?.templateName}
                                            name="templateName"
                                            placeholder={t("placeholders.templateName")}
                                            onChange={(e) => handleFromChange(e)}

                                        />
                                        {errors?.templateName && (
                                            <CustomError text={errors.templateName} />
                                        )}
                                    </div>
                                </div>
                                <div className="col-md-5">
                                    <div className="form-group mb-2">
                                        <Label text={t("labels.description")} required={true} />

                                        <Input
                                            type="text"
                                            value={salaryTempFormData?.description}
                                            name="description"
                                            placeholder={t("placeholders.salaryTemplateDescription")}
                                            onChange={(e) => handleFromChange(e)}

                                        />
                                        {errors?.description && (
                                            <CustomError text={errors.description} />
                                        )}
                                    </div>
                                </div>
                            </div>}
                            <div className='cardBox ' style={{ padding: "5px 0px" }}>
                                <div style={{ padding: "6px 35px" }}>
                                    <div className="row">
                                        <div className="col-md-4 pt-2 ">
                                            <div className="form-group">
                                                <CustomStrong text={"Employee Salary (Monthly CTC)"}
                                                    className={"text-primary mt-2"}
                                                    required={true} />

                                            </div>
                                        </div>
                                        <div className="col-md-5 p-0">
                                            <div className="form-group mb-3">
                                                <div className="input-group mb-3">
                                                    <span className="input-group-text" id="basic-addon1">
                                                        {getInputPrefix('')}</span>
                                                    <Input
                                                        type="number"
                                                        placeholder="Enter Monthly CTC"
                                                        value={salaryTempFormData?.ctc}
                                                        name="ctc"
                                                        min={100}
                                                        max={5000000}
                                                        onChange={(e) => handleFromChange(e)}
                                                        onBlur={() => generateSalaryTemplate()}
                                                    />

                                                </div>
                                                {errors?.description && (
                                                    <CustomError text={errors.ctc} />
                                                )}

                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <hr style={{ margin: "0px" }} />
                                {generateSalaryTemplatePending ?
                                    <CustomSpinner isShow={generateSalaryTemplatePending} size="xl" />
                                    : employeeSalaryDetail && <div className=" p-2">
                                        {/* Rendering CTC Generate */}
                                        {renderedCTCGenerate()}
                                    </div>}
                            </div>
                        </div>
                    </div>

                </div>

                <div className="footer">
                    <div className="d-flex align-items-center justify-content-end gap-3 mt-3">
                        {!selectedSalaryTemplate ?
                            <CustomButton
                                text={selectedSalaryTempData ? t("btns.update") : t("btns.submit")}
                                classNames={"btn-primary "}
                                handleClick={() => submitSalaryTemp()}
                                isLoading={saveSalaryTemplatePending}
                            /> : <CustomButton
                                text={t("btns.assignSalary")}
                                classNames={"btn-primary "}
                                handleClick={() => assignedSalaryTemp()}
                                isLoading={addEmployeeSalaryPending}
                            />}
                    </div>
                </div>
            </div>

            {
                isOpenDeleteModal && (
                    <ApproveRejectModal
                        onApproveReject={toggleDeleteModal}
                        isShow={isOpenDeleteModal}
                        desc={t("alerts.deletePopup.desc").replace('$1', "Filed")}
                        btnRightLoading={calculateCtcPending}
                        btnTextRight={t("btns.delete")}
                        btnTextLeft={t("btns.cancel")}

                    />)
            }
            {openPdf &&
            <FullScreenPreviewModal
            isOpen={openPdf}
            onClose={() => setOpenPdf(false)}
            fileUrl={offerLetterData?.fileUrl}
            fileType={offerLetterData?.fileType}
            fileName={offerLetterData?.filename}
            isSendMail={()=>sendOfferLetterMail()}
            isShowSendBtn ={true}
            />
            }

            {sendOfferLetterPending && 
                  <CustomSpinner
                  isShow={sendOfferLetterPending}
                  size="lg"
                  />
                }

        </div >
    )
}

export default CreateUpdateSalaryTemplate
